.filter{
    width: 14%;
    @include font-desc(1.6rem, 1.9rem);
    min-width: 185px;
}
.filter-item{
    margin-bottom: 33px;
}
.filter-item__title {
    margin-bottom: 15px;
}
.filler-item__list{
    display: flex;
    justify-content: space-between;    
}
.filler-item__list input{
    width: 100px;
    height: 24px;
    border: 1px solid #E1E1E1;
    box-sizing: border-box;
    @include font-desc();
    padding-left: 23px;  
    margin-right: 10px;
    margin-left: -17px;      
}
.filler-item__list label{
    position: relative;
    left: 4px;
    top: 4px;
}
.filter-item__title{
    width: 100%;
}
.filter-item--area .filler-item__list{
    width: 100%;
    flex-wrap: wrap;
}
.filler-item__container{
    display: flex;
    width: 100%;
    margin-bottom: 18px;  
    @include font-desc(); 
}
.filter-item--area input{
    width: 20px;
    height: 20px;
    border: 1px solid #E1E1E1;
    box-sizing: border-box;
    margin-left: 0px;
}
.filler-item__container input[type="checkbox"] {
    display: none;
}
.filler-item__container label{
    left: 30px;
}
.filler-item__container label::before{
    content: " ";
    position: absolute;
    width: 20px;
    height: 20px;
    border: 1px solid #E1E1E1;
    box-sizing: border-box;
    left: -30px;
    top: -2px;
    letter-spacing: 1px;
}
input[type="checkbox"]:checked + label::after {
    content: "";
    position: absolute;    
    background: url(../img/arrow.svg) no-repeat;
    width: 9px;
    height: 8px;
    left: -25px;
    top: 4px;
    background-position: 0px 0px;    
}
.filter-item--facilities .filler-item__list{
    display: flex;
    flex-wrap: wrap;
    margin-top: 13px;
}
.filter-item--area{
    margin-bottom: 22px;
}
.filter__btn{
    width: 185px;
    height: 42px;
    background: #FAC663;
    border: 2px solid #FAC663;
    box-sizing: border-box;
    border-radius: 25px;
}
.filter__btn--white{
    margin-top: 11px;
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}