.sort-form{
    @include font-desc(1.4rem, 1.7rem);
    min-width: 174px;   
    height: 35px;    
}
.sort-form select:focus{
    box-shadow: 0px 2px 12px rgba(173, 152, 143, 0.18);
}
.sort-form__select{
    min-width: 174px;
    background: #FFFFFF;    
    border-radius: 4px;
    height: 35px;
    padding-left: 30px;     
    outline: none;
    position: relative;    
}

.sort-form__select{  
    background: url(../img/sort-trigger--start.svg) no-repeat right;  
    -webkit-appearance: none; 
    background-position: 143px 12px;       
}
.sort-form__select:focus{
    background: url(../img/sort-trigger.svg) no-repeat right;
    background-position: 143px 12px; 
}
.sort-form__select:blur{  
    background: url(../img/sort-trigger--start.svg) no-repeat right;  
    -webkit-appearance: none; 
    background-position: 143px 12px;       
}
