body{
    width: 100%;
}
.main-wrapper{
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
}
.main-header{
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-top: 12px;
    background: #FFFEFD;
}
.main-title{
    @include font-title(3.6rem, 4.3rem);
    color: #151B28;
    margin-top: 76px;
    text-align: left;    
    letter-spacing: 1px;
    margin-bottom: 63px;
}
.wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.wrapper--second{
    align-items: start;
}
button{
    outline: none;
}