@mixin font-title($size:2rem, $height:2.4rem) {
    font-family: $font-title;
    color: $text-color;
    font-size: $size;
    font-weight: 600;
    line-height: $height;
    text-align: left;    
  }
  @mixin font-desc($size:1.4rem, $height:1.7rem) {
    font-family: $font-desc;
    color: $text-color;
    font-size: $size;
    font-weight: 400;
    line-height: $height;
    text-align: left;  
  }